.App {
  text-align: center;
}

.logo {
  height: 30vmin;
  pointer-events: none;
}

.App-header {
  background-color: #020520;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: rgba(255, 255, 255, 0.85);
}
.link-github {
  color: rgba(255, 255, 255, 0.6);
  text-decoration: none;
}

.link-github:active {
  color: #5ffab9;
}

.link-github:visited {
  color: rgba(255, 255, 255, 0.6);
}

@keyframes glow {
  0% {
    color: rgba(255, 255, 255, 0.6);
  }
  50% {
    color: rgb(255, 210, 125);
  }
  100% {
    color: rgba(255, 255, 255, 0.6);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .glow {
    animation: glow infinite 5s linear;
  }
}
